.form {
    display: grid;
    justify-content: center;
    grid-template-columns: 1fr auto;
    grid-gap: 25px;
    padding-top: 15px;
}

@media (max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}

.form__input {
    padding: 5px;
    border: solid 1.5px;
    border-color: #ddd;
}

.form__button {
    padding: 10px;
    border: none;
    background-color: teal;
    color: white;
    font-size: 15px;
}

.form__button:hover {
    transition: 1s;
    transform: scale(1.1);
    background-color: rgb(11, 171, 171);
    cursor: pointer;
}

.form__button:active {
    background-color: hsl(180, 88%, 45%);
}